<template>
  <div class="head">
    <van-nav-bar title="芙蓉路项目" fixed left-arrow @click-left="onClickLeft"/>
    <van-field/>
    <van-panel :title="content.title">
      <div>{{content.detail}}</div>
    </van-panel>
    <p class="width"></p>
    <van-cell-group>
      <van-cell title="招募人数" :value="content.num"/>
      <van-cell title="报名起止" is-link :value="content.afdate"/>
      <van-cell title="活动地点" is-link :value="content.address"/>
      <van-cell title="咨询人" is-link :value="content.consultanter"/>
    </van-cell-group>
    <p class="width"></p>
    <van-row>
      <van-col span="23" offset="1">
        <div class="bm">
          <img :src="content.img">
          <van-cell :title="content.title" is-link label="这个人很懒，什么也没有留下"/>
        </div>
      </van-col>
    </van-row>

    <p class="width">招募详情</p>
    <van-row>
      <van-col span="24">
        <van-cell title="报名方式" label="adipisicing elit. Ea, is"/>
        <van-cell
          title="邮件主旨"
          label="adipisicing elit. Ea, isadipisicing elit. Ea, isadipisicing elit. Ea, is"
        />
        <van-cell
          title="招募要求"
          label="adipisicing eit. Ea, isadipisicing elit. Ea, isadit. Ea, isadipisicing elit. Ea, isadlit. Ea, isadipisicing elit. Ea, isadipisicing elit. Ea, is"
        />
        <van-cell
          title="招募范围"
          label="adipisicing elit. Ea, isadipisicing elit. Ea, isadipisicing elit. Ea, is"
        />
        <van-cell
          title="活动内容"
          label="adipisicing elit. Ea, isadipisicing elit. Ea, isadipisicing elit. Ea, is"
        />
        <van-cell
          title="招募基地"
          label="adipisicing elit. Ea, isadipisicing elit. Ea, isadipisicing elit. Ea, is"
        />
      </van-col>
    </van-row>
    <van-tabbar v-model="active">
      <van-tabbar-item to="baoming">报名</van-tabbar-item>
    </van-tabbar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      active:0
    };
  },
  methods: {
    onClickLeft() {
     
      this.$router.go(-1);
    return { active: "", content: "" };
  }
  },
  methods: {
    onClickLeft() {
      this.$router.push("/main");
    }
  },
  mounted() {
    var _this = this;
    this.axios({
      headers: { "Content-Type": "application/x-www-form-urlencoded" },
      url: "http://101.132.164.103:8080/together/activityid.do",

      params: { id: _this.$route.params.id }
    }).then(function(data) {
      console.log(data.data.info);
      _this.content = data.data.info;
    });
  }
};
</script>
<style scoped>
.width {
  background: #f2f2f9;
  height: 10px;
  margin: 0 auto;
}
.van-icon-user-circle-o:before {
  font-size: 26px;
}
.head > p:nth-of-type(3) {
  height: 30px;
  line-height: 30px;
  font-size: 12px;
}
.van-panel div {
  padding: 6px 0;
  width: 360px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.bm {
  display: flex;
  align-items: center;
}
.bm img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.van-tabbar-item {
  background-color: #e8e8ea;
  color: black;
}
</style>



